<template>
  <div id="music">
    <div id="title">
      <h1 class="select2">MusicPlayer</h1>
      <ul id="head">
        <li class="select2">
          <el-button>播放列表</el-button>
        </li>
        <li class="select2"><el-button>歌单</el-button></li>
        <li>
          <input
            type="text"
            id="search"
            v-model="search"
            @keyup.enter="searchMusic"
          />
        </li>
        <li class="select2">
          <el-button
            id="searchMusic"
            icon="el-icon-search"
            @click.native="searchMusic"
            >歌曲搜索</el-button
          >
        </li>
      </ul>
    </div>
    <router-view :result="result"></router-view>
    <div id="load">
      <div>
        <span><i class="el-icon-d-arrow-left select"></i></span>
        <span
          ><i
            :style="pauseStyle"
            @click="pause"
            class="el-icon-video-pause select"
          ></i
        ></span>
        <span
          ><i
            :style="playStyle"
            @click="playMusic"
            class="el-icon-video-play select"
          ></i
        ></span>
        <span><i class="el-icon-d-arrow-right select"></i></span>
      </div>
      <div class="block">
        <span class="demonstration"></span>
        <el-slider v-model="load"></el-slider>
      </div>
      <div>
        <span><i class="el-icon-bell select"></i></span>
        <span class="demonstration1"></span>
        <el-slider
          style="width: 65%; margin-right: 20px"
          v-model="sound"
        ></el-slider>
      </div>
      <audio
        ref="music"
        autoplay
        controls
        :src="url"
        style="display: none"
        @timeupdate="timeupdate"
      ></audio>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      url: "",
      search: "",
      load: 0,
      sound: 100,
      SongName: "hello",
      result: [],
      isPlay: false,
      pauseStyle: {},
      playStyle: {},
    };
  },
  methods: {
    timeupdate() {
      this.load = this.$refs.music.currentTime;
    },
    pause() {
      this.$refs.music.pause();
      this.isPlay = false;
      this.playStyle = {
        color: "rgba(225, 225, 225, 0.6)",
        transform: "scale(1)",
      };
      this.pauseStyle = { color: "white", transform: "scale(1.1)" };
    },
    playMusic() {
      this.$refs.music.play();
      this.isPlay = true;
      this.playStyle = { color: "white", transform: "scale(1.1)" };
      this.pauseStyle = {
        color: "rgba(225, 225, 225, 0.6)",
        transform: "scale(1)",
      };
    },
    searchMusic() {
      axios
        .get(`http://cloud-music.pl-fe.cn/search?keywords=${this.search}`)
        .then(
          (response) => {
            console.log("发送成功");
            console.log(response.data);
            this.result = response.data.result.songs;
            console.log(this.result);
          },
          (error) => {
            console.log(error.data);
          }
        );
    },
  },
  mounted() {
    this.$bus.$on("receiveUrl", (data) => {
      this.url = data;
    });
    //搜索歌曲
    // http://cloud-music.pl-fe.cn/search?keywords=
    // 获取歌单
    // https://music.163.com/api/playlist/detail?id=2695791551
    //获取歌曲
    // https://music.cyrilstudio.top/song/url?id=11975048
    //获取用户
    // https://music.163.com/api/v1/user/detail/1583080072
    // axios.get("https://music.163.com/api/playlist/detail?id=2695791551").then(
    //   (response) => {
    //     console.log("发送成功", response.data);
    //   },
    //   (error) => {
    //     console.log("发送失败" + error.data);
    //   }
    // );
  },
  watch: {
    sound() {
      let audioPlayer = this.$refs.music;
      audioPlayer.volume = this.sound / 100;
    },
  },
};
</script>

<style scoped>
.isSelect {
  color: white;
  transform: scale(1.1);
}
.noSelect {
  color: rgba(225, 225, 225, 0.6);
}
#search {
  height: 35px !important;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(255, 255, 255, 0.4);
  box-shadow: 0px 0px 5px 2px grey;
  border-radius: 2px;
  color: white;
}
i {
  font-size: 30px;
}

.select:hover {
  color: rgba(255, 255, 255, 1);
  transform: scale(1.1);
  cursor: pointer;
}
.select2:hover {
  color: white;
}
#head .el-button:hover {
  background-color: grey;
}
#load div:nth-child(1),
div:nth-child(3) {
  height: 40px;
  width: 20%;
  color: rgba(225, 225, 225, 0.6);
  display: flex;
  justify-content: space-around;
  background-color: rgba(255, 255, 255, 0);
}

#load div:nth-child(2) {
  height: 20px;
  flex: 1;
  margin-right: 20px;
}

#load {
  position: absolute;
  bottom: 3%;
  left: 10%;
  width: 80%;
  display: flex;
  justify-content: space-between;
}
.el-button {
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(255, 255, 255, 0.4);
  box-shadow: 0px 0px 5px 2px grey;
  border-radius: 2px;
  color: white;
}
#music {
  height: 100vh;
  width: 100%;
  background: linear-gradient(to top right, rgb(0, 0, 0), rgb(121, 110, 120));
  overflow: hidden;
}

#title {
  margin-top: 50px;
  color: rgba(225, 225, 225, 0.8);
  font-family: "思源宋体 CN";
  width: 100%;
  margin-left: 50px;
}

#head {
  margin-left: 10%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 30%;
}

#head li {
  /* margin-left: 15%; */
  margin-right: 20px;
  flex: 1;
}
#head li:nth-child(3) {
  flex: 3;
}
</style>